import { required, email, minLength, maxLength, sameAs } from "vuelidate/lib/validators";
import Swal from 'sweetalert2';
import catalogApi from '@/api/catalogApi'
export default {
  middleware: "auth",
  auth: "guest",
  data() {
    return {
      form: {
        email: null,
        phone: null,
        password: null,
        re_password: null,
        Date: new Date().toISOString().substr(0, 10)
      },
      listEmail: [],
    }
  },
  validations:{
    form: {
      email: { required, emailValidator: email},
      password: { required, minLength: minLength(6) },
      phone: {required, maxLength: maxLength(10)},
      re_password: { sameAs: sameAs('password') }
    }
  },
  async created(){
      await this.AllListEmail();
    },

  computed:{
    isFormValid(){
        return !this.$v.form.$invalid;
    }
  },
  methods: {
    async AllListEmail(){
      try{
        const {data} = await catalogApi.getAllEmail(this.$axios)
        this.listEmail = data.accs
        console.log(this.listEmail);
      }catch(err){console.log(err);}
    },
    
    // eslint-disable-next-line require-await
    async onClickEmail(){
      if (this.listEmail.length === this.form.email){
        console.log("Email da ton tai");
      }
    },

    async register() {
      const data = {
        Email: this.form.email,
        Phone: this.form.phone,
        Password: this.form.password,
        CreateAt: this.form.Date
      }
      try {
        const res = await this.$axios.$post('/account/signup', data)
        console.log(res)
        // console.log('Signup successfully!')
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
          },
        })

        Toast.fire({
          icon: 'success',
          title: 'Signed up successfully',
        })
        console.log(this.$router)
        this.$router.push('/signin')
      } catch (error) {
        console.log(error)
      }
    },
  },
}
